
body {
  margin: 0;
}
html,
body,
#app {
  height: 100%;
  overflow: hidden;
  font-family: "VT323", monospace;
  font-size: 3.5vh;
  color: #55c1ff;
  touch-action: none;
  background-color: #122932;
  border-bottom: 1px solid #55c1ff;
}
.u-textCenter {
  text-align: center;
}
.u-inline {
  display: inline-block;
}
.u-cursorNormal {
  cursor: default;
}
.u-nomargin {
  margin: 0px;
}
.u-hidden {
  visibility: hidden;
}
.u-pb1 {
  padding-bottom: 1em;
}

.Game {
  height: calc(100% - 40px);
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
}
#dragShadow {
  position: absolute;
  top: -100vh;
}

.GameSettings {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.GameSettings-checkbox {
    padding-bottom: 10%;
}

.BaseButton {
  padding: 5px;
  padding-right: 20px;
  padding-left: 20px;
  font-family: "VT323", monospace;
  font-size: 3.5vh;
  color: #122932;
  cursor: pointer;
  background-color: #55c1ff;
  border: 1px solid #a3b18a;
  border-radius: 5%;
  box-shadow: 1px 1px 1px #a3b18a;
}

.BaseCheckbox {
  position: relative;
  display: block;
  padding-left: 1.5em;
}
.BaseCheckbox-tick {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    height: 0.6em;
    width: 0.6em;
    background-color: #122932;
    border: 0.2em solid #55c1ff;
    border-radius: 0.3em;
}
.BaseCheckbox-tick--active {
    background-color: #122932;
}
.BaseCheckbox-tick--active:after {
      content: 'x';
      position: relative;
      font-size: 0.8em;
      top: -0.4em;
      left: 0.15em;
}

.GameHeader {
  height: 10%;
  max-height: 10%;
  display: flex;
  justify-content: space-between;
}
.GameTitle {
  flex-grow: 2;
  padding-bottom: 5px;
  margin: 0;
  font-size: 6.5vh;
  text-align: center;
  vertical-align: middle;
}
.BackButton {
  flex-grow: 1;
  padding-left: 10px;
  font-size: 6.5vh;
  font-weight: bold;
  letter-spacing: -0.3em;
  cursor: pointer;
}
.HelpButton {
  flex-grow: 1;
  position: relative;
  padding-right: 10px;
  right: 0px;
  font-size: 6.5vh;
  font-weight: bold;
  cursor: pointer;
}

.GameBoard {
  height: 92%;
  max-height: 92%;
}

.guesses-enter {
  transform: translateY(100%);
}
.guesses-enter-to {
  transform: translateY(0);
}
.GuessList {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 70%;
  max-height: 70%;
  overflow-y: auto;
}
.GuessRow {
  display: grid;
  transition: transform 1s;
  grid-template-columns: repeat(4, 1fr) 2fr;
  align-items: center;
  justify-items: center;
  height: 19%;
  min-height: 19%;
  border-top: 1px solid #a3b18a;
}
.GuessRow-result {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    align-self: stretch;
    justify-items: center;
    justify-self: stretch;
    border-left: 1px solid #a3b18a;
}

.Pin {
  width: 5vh;
  height: 5vh;
  border: 1px solid black;
  border-radius: 50%;
  box-shadow: 1px 1px 2px black;
}
.Pin--empty {
  background-color: transparent;
  box-shadow: inset 1px 1px 2px black;
}
.Pin--dragable {
  cursor: url(https://ssl.gstatic.com/ui/v1/icons/mail/images/2/openhand.cur), default !important;
}
.Pin--fontSize {
  width: 3.5vh;
  height: 3.5vh;
}
.Pin--PIN1 {
  background-color: #ff0;
}
.Pin--PIN2 {
  background-color: #f41111;
}
.Pin--PIN3 {
  background-color: #17c50e;
}
.Pin--PIN4 {
  background-color: #3320a9;
}
.Pin--PIN5 {
  background-color: #ff7c00;
}
.Pin--PIN6 {
  background-color: #c30e74;
}

.Pin--small {
  width: 3vh;
  height: 3vh;
}
.Pin--exactMatch {
  background-color: black;
}
.Pin--colorMatch {
  background-color: white;
}

.CurrentGuess {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  align-items: center;
  justify-items: center;
  height: 14.28%;
  border-top: 1px solid #a3b18a;
  border-bottom: 1px solid #a3b18a;
}
.CurrentGuess-testButton {
    grid-column: -3 / span 2;
}

.PinOptions {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  align-items: center;
  justify-items: center;
  height: 14.28%;
}

.GameEnd {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.GameEnd-resultText {
    font-size: 6.5vh;
}
.GameEnd-resultCode {
    width: 80%;
    text-align: center;
}
.GameEnd-code {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
    justify-items: center;
}
.GameEnd-backButton {
    margin-top: 1em;
}

.GameHelp {
  max-height: 80%;
  height: 80%;
  padding-left: 10px;
  padding-right: 10px;
}
.HelpGuessRow {
  display: grid;
  transition: transform 1s;
  grid-template-columns: repeat(4, 1fr) 2fr;
  align-items: center;
  justify-items: center;
  height: 14%;
  min-height: 14%;
  border-top: 1px solid #a3b18a;
  border-bottom: 1px solid #a3b18a;
}
.HelpGuessRow-result {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    align-self: stretch;
    justify-items: center;
    justify-self: stretch;
    border-left: 1px solid #a3b18a;
}

#DragShadow {
  position: absolute;
  top: -150px;
}
